import * as Sentry from "@sentry/browser";
import {chrome} from "../polyfill.ts";
import { TARGET_BROWSER } from "@/app/scripts/browser-const.js";
import { BROWSER_NAME } from "@/app/scripts/app-consts.js";

export function sentryInit(_options = {}) {
    if (TARGET_BROWSER !== BROWSER_NAME.Firefox) {
        console.debug('sentryInit. ENVIRONMENT: ', ENVIRONMENT);
    
        // Check if this is a beta version by looking at the extension name
        let manifest = {};
        let isBetaVersion = false;
        try {
            manifest = chrome.runtime.getManifest();
            isBetaVersion = manifest.name.includes('Beta');
        } catch (e) {
            console.warn('sentryInit: Could not get manifest or determine if beta version.', e);
        }
    
        // Set different sampling rates for beta vs production users
        const betaSampleRate = 0.1; // 10% for beta users
        const productionSampleRate = 0.005; // 0.05% for production users
        const betaTracesSampleRate = 0.01; // 1% for beta users
        const productionTracesSampleRate = 0.00025; // 0.0025% for production users
    
        let params = {
            dsn: "https://708a324f1a7ec87a2658346fb4bb7586@o438337.ingest.sentry.io/4506062147551232",
            tracesSampleRate: isBetaVersion ? betaTracesSampleRate : productionTracesSampleRate,
            sampleRate: isBetaVersion ? betaSampleRate : productionSampleRate,
            // Session Replay
            replaysSessionSampleRate: 0, // This sets the sample rate at 10%. You may want to change it to 100% while in development and then sample at a lower rate in production.
            replaysOnErrorSampleRate: 0,
            // This variable is set in Webpack itself (see webpack.DefinePlugin)
            enabled: ENVIRONMENT === 'production',
            release: chrome.runtime.getManifest().version,
            attachStacktrace: true,
            ignoreErrors: [ // https://docs.sentry.io/platforms/javascript/configuration/filtering/
                'Could not establish connection. Receiving end does not exist',
                'No tab with id:',
                'Failed to fetch',
                'Corruption: block checksum mismatch',
                'QuotaExceededError: Encountered full disk',
                'Non-Error promise rejection captured with value: IO error',
                'FILE_ERROR_NO_SPACE',
                'FILE_ERROR_IO',
                'FILE_ERROR_FAILED',
                'FILE_ERROR_ACCESS_DENIED',
            ]
        };
    
        if (isBetaVersion) {
        // eslint-disable-next-line max-len
            console.debug(`Sentry - Beta: ${isBetaVersion}, Sample: ${params.sampleRate}, Traces: ${params.tracesSampleRate}`);
        }
    
        Sentry.init(params);
        Sentry.getCurrentScope().setLevel("error");
    }
}
